.header-color{
  background: #525252;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #3d72b4, #525252);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #3d72b4, #525252); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  background: #525252;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3d72b4, #525252);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3d72b4, #525252); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  
}

.avatar{
width: 15rem;
}

.banner-text{
  background: #0f0c29;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

color: white;
border-radius: 10px;
width: 80%;
margin: auto;
padding-top: 1rem;
}

.banner-text hr{
  width: 60%;
  margin: auto;
  border-top: 5px dotted white;
}

.social-links{
  display: flex;
  justify-content: space-around;
  padding: 2rem 3rem;
}

.social-links i{
  color: white;
  font-size: 4rem;
}

.projects-grid{
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.card-btns{
  display: flex;
  justify-content: space-around;
}

/* Contact Page */
.contact-grid{

  text-align: center;
}

.list-flex{
  display: flex;
  flex-direction: column;
margin-top: 4rem;

  align-items: center;
  justify-content: center;

}

.contact-grid h5{
  line-height: 2rem;
}
.contact-avatar{
  width: 40%;
}

.contact-detail{
  font-size: 1.5rem;

}

.contact-grid i{
  font-size: 2rem;
  padding-right: 1rem;
}

.myHr{
  width: 60%;
  margin: auto;
  border-top: 5px dotted #333;
}

/* Resume */
.resume-right{
  background-color: #333;
  color: #fff;
}

.resumeHr{
  color: red;
  border-top: 5px solid #fff;
  width: 80%;
  margin: auto;

}

/* Footer */
.footer{
  height: 1rem;
  text-align: center;
  
 
}


@media only screen and (max-width: 600px) {
  .projects-grid{
    flex-direction: column;
  }
}